	module.exports = {"data":{"markdownRemark":{"htmlAst":{"type":"root","children":[{"type":"element","tagName":"p","properties":{},"children":[{"type":"element","tagName":"page-intro","properties":{},"children":[{"type":"text","value":"Carbon is committed to following and complying with best practices when it comes to accessibility. Each component and element was built following the Web Content Accessibility Guidelines (WCAG) and met the AA standard. Our patterns are perceivable, operable, and understandable to users, even when using a screen reader or other assistive technology."}]}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h2","properties":{},"children":[{"type":"text","value":"Keyboard accessibility"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Keyboard accessibility is important for users who rely on or prefer using a keyboard. Encourage accessibility by providing keyboard functionality to all available content. Common keyboard interactions include using the "},{"type":"element","tagName":"code","properties":{},"children":[{"type":"text","value":"Tab"}]},{"type":"text","value":" key to select different interactive elements on a page and using the "},{"type":"element","tagName":"code","properties":{},"children":[{"type":"text","value":"Enter"}]},{"type":"text","value":" key or the Spacebar to activate an in-focus element."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h3","properties":{},"children":[{"type":"text","value":"Focus indicators"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"The "},{"type":"element","tagName":"code","properties":{},"children":[{"type":"text","value":"tab"}]},{"type":"text","value":" key navigates through various interactive elements on a page. A default visual indicator is provided by the web browser in use. The display is an outlined border around the focused element. When an element is in focus, it can be further activated using the keyboard."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h3","properties":{},"children":[{"type":"text","value":"Navigation order"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"The order in which interactive elements receive focus is logical and predictable. Create the tab flow hierarchy by using the source code to arrange the keyboard navigation. Begin with the header, followed by the main navigation, then page navigation (from left to right, top to bottom), and end with the footer."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Use natively-accessible elements in navigation to activate links, buttons, and form controls with a keyboard. Reinforce semantic HTML to convey intent and meaning instead of simply defining the look and feel of an element. Enhance with ARIA (Accessible Rich Internet Application) labels when necessary. For additional customization, use "},{"type":"element","tagName":"code","properties":{},"children":[{"type":"text","value":"tabindex=“0”"}]},{"type":"text","value":" to ensure a navigable element receives a focus indicator."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Provide a “Skip to content” option when there is lengthy navigation so a user may access main content faster. This option is a link that is visually hidden by default and appears when it receives keyboard focus via the "},{"type":"element","tagName":"code","properties":{},"children":[{"type":"text","value":"tab"}]},{"type":"text","value":" key."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"element","tagName":"a","properties":{"href":"http://webaim.org/techniques/skipnav/","target":"blank"},"children":[{"type":"text","value":"WebAIM"}]},{"type":"text","value":" recommends the following to enable a “Skip to content” link."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"pre","properties":{},"children":[{"type":"element","tagName":"code","properties":{"className":["language-html"]},"children":[{"type":"text","value":"<body>\n    <a href=\"#maincontent\">Skip to main content</a>\n    ...\n    <main id=\"maincontent>\n        <h1>Heading</h1>\n        <p>This is the first paragraph</p>\n    </main>\n</body>\n"}]}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h2","properties":{},"children":[{"type":"text","value":"Color accessibility"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Please refer to our "},{"type":"element","tagName":"a","properties":{"href":"/style/colors/usage"},"children":[{"type":"text","value":"usage"}]},{"type":"text","value":" section on the Colors page to learn more about color contrast ratios. You can also use this "},{"type":"element","tagName":"a","properties":{"href":"https://marijohannessen.github.io/color-contrast-checker/"},"children":[{"type":"text","value":"color contrast"}]},{"type":"text","value":" tool to ensure your ratios pass WCAG AA standards."}]}],"data":{"quirksMode":false}},"fields":{"slug":"/guidelines/accessibility/keyboard","currentPage":"keyboard"},"frontmatter":{"title":"Accessibility","label":"Guidelines","tabs":["Color","Keyboard"]}}},"pathContext":{"slug":"/guidelines/accessibility/keyboard","currentPage":"keyboard"}}

